$post-title-max-width: 1100px !default;
$post-title-margin: 0 auto !default;
$post-title-padding: 20% 15px !default;

$post-title-shadow: -.06em .06em .02em rgba($shadow-color, .5);
$post-byline-shadow: -.04em .04em .02em rgba($shadow-color, .5);

$post-author-img-border: .3em solid $base-background-color;

.post-header {
  background-position: center;
  background-size: cover;
  display: block;
  position: relative;
}

.post-title {
  margin: $post-title-margin;
  max-width: $post-title-max-width;
  padding: $post-title-padding;
  text-align: center;
  z-index: 1;
}

.post-title-header {
  color: lighten($highlight-color, 10%);
  font-family: $sans-serif;
  font-size: $base-font-size * 5;
  font-weight: 700;
  line-height: $heading-line-height;
  margin-bottom: 25px;
  text-shadow: $post-title-shadow;
  text-transform: uppercase;
}

.post-byline {
  color: lighten($highlight-color, 10%);
  font-family: $serif;
  font-size: $base-font-size * 1.2;
  font-style: italic;
  font-weight: 400;
  line-height: $heading-line-height;
  text-shadow: $post-byline-shadow;
}

.post-author-img {
  background: $base-background-color;
  border: $post-author-img-border;
  border-radius: 50%;
  bottom: em(-40);
  height: em(80);
  position: absolute;
  right: em(15);
  width: em(80);
}

.post-content {
  display: block;
  margin: 0 $base-spacing;
}

.post-body {
  box-sizing: content-box;
  margin: 0 auto;
  padding: $base-spacing;
  max-width: $max-width;
}

.post-comments {
  margin: 0 $base-spacing;
  padding: $base-spacing;
}
